.container {
    height: 100%;
    display: flex;
    justify-content: center;
}

 .loader {
     width: 100%;
     height: 100%;
     display: flex;
     justify-content: center;
     align-items: center;
     font-weight: 300;
 }

 .movies {
    display: grid;
    grid-template-columns: repeat(2, minmax(400px, 1fr));
    grid-gap: 100px;
    padding-top: 70px;
    padding-left: 10%;
    width: 90%;
}

@media screen and (max-width: 1090px) {
    .movies {
        grid-template-columns: 1fr;
        width: 100%;
    }
}